<template>
	<view>
		<view class="colonn">
			
			<input  v-model="gengxinUrl" placeholder="请输入热更新地址"/>
			
			<view class="roww fs-30 rowsa p-left-20 p-right-20">
				<progress style="width: 680rpx;" :percent="numJindu"></progress>
				<view class="w-40"></view>
				<view>0%</view>
			</view>
			<button size="default" type="primary"
				style="width: 750rpx;height: 80rpx;line-height: 80rpx;border-radius: 0rpx;"
				@click="getIsGengxin"
				>检查更新</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				isGengxin:true,
				numJindu:0,
				gengxinUrl:"",
			}
		},
		methods: {
			// 获取是否更新
			getIsGengxin(){  
				
			},
			toGengxin(url) {
				const downloadTask = uni.downloadFile({
					url:url,
					success: res => {
						if (res.statusCode !== 200) {
							console.error('下载安装包失败', err);
							return;
						}
						// 下载好直接安装，下次启动生效
						plus.runtime.install(res.tempFilePath, {
							force: false
						}, () => {
							uni.hideLoading()
							if (is_mandatory) {
								//更新完重启app
								plus.runtime.restart();
								return;
							}
							uni.showModal({
								title: '安装成功是否重启？',
								success: res => {
									if (res.confirm) {
										//更新完重启app
										plus.runtime.restart();
									}
								}
							});
						}, err => {
							uni.hideLoading()
							uni.showModal({
								title: '更新失败',
								content: err.message,
								showCancel: false
							});
						});
					},
					//接口调用结束
					complete: () => {
						
						uni.hideLoading();
						downloadTask.offProgressUpdate(); //取消监听加载进度
					}
				});
				//监听下载进度
				downloadTask.onProgressUpdate(res => {
					// state.percent = res.progress;
					if(res.progress>=100){ 
						this.isGengxin=false;
					}
					this.numJindu=res.progress;
					waiting.setTitle("正在下载 - " + res.progress + "%");
					// console.log('下载进度百分比:' + res.progress); // 下载进度百分比
					// console.log('已经下载的数据长度:' + res.totalBytesWritten); // 已经下载的数据长度，单位 Bytes
					// console.log('预期需要下载的数据总长度:' + res.totalBytesExpectedToWrite); // 预期需要下载的数据总长度，单位 Bytes
				});
			}
		}
	}
</script>

<style>
	@import url(gengxin.css);
</style>
